import  React, {useEffect, useState} from 'react';
import { Link } from "react-scroll"; 
import { Navbar, Nav } from 'react-bootstrap/';

function Navigation() {

  var navBarToggler;

  const [width, setWidth] = useState(0)

  function closeNav(){
    if ( width < 991 && navBarToggler !== undefined){
      setTimeout( () => {
        navBarToggler.click();
      }, 10);
    }
  }

  useEffect(() => {
    function handleResize() {
      setWidth(window.innerWidth);
      
    }
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [width]);


  return (
    <Navbar expand="lg" className="fixed-top">
        <Navbar.Brand href="#" className="ml-4">Manikiran's Portfolio</Navbar.Brand>
        <Navbar.Toggle ref={toggler => navBarToggler = toggler }>
          { width < 992 && <div className="nav-icons d-flex flex-column">
                <span className="icon-bar mt-1"></span>
                <span className="icon-bar"></span>
                <span className="icon-bar"></span> 
             </div>            
          }      
        </Navbar.Toggle>               
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto"> 
          <Link 
                onClick={closeNav}
                className="nav-item" 
                to="section1"
                spy={true}
                smooth={true}
                duration={700}
              >Plastic Reducer</Link>
          <Link 
                onClick={closeNav}
                className="nav-item" 
                to="section2"
                spy={true}
                smooth={true}
                duration={700}
              >L Music Player</Link>
              <Link 
                onClick={closeNav}
                className="nav-item" 
                to="section3"
                spy={true}
                smooth={true}
                duration={700}
              >Food Order</Link>
               <Link 
                onClick={closeNav}
                className="nav-item" 
                to="section4"
                spy={true}
                smooth={true}
                duration={700}
              >Fast News</Link>
              <Link 
                onClick={closeNav}
                className="nav-item"
                to="section5"
                spy={true}
                smooth={true}
                duration={700}
                >Bluetooth Beacons</Link>
              <Link 
                onClick={closeNav}
                className="nav-item" 
                to="section6"
                spy={true}
                smooth={true}
                duration={700}
              >Hire Manikiran ?</Link>           
          </Nav>            
        </Navbar.Collapse>
      </Navbar>    
  );
}

export default Navigation;
